import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Search, Group, Footer, List, Cell, FormLayout, InfoRow, Progress, Div, View, IOS, platform} from '@vkontakte/vkui';
import '../style/style.css'
import image1 from '../img/image1.png';
import image2 from '../img/image2.png';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
   
const osname = platform();

const Profile = ({ id, go, fetchedUser }) => (
	<View activePanel="group">
    <Panel id="group">
      <PanelHeader>left={<HeaderButton onClick={go} data-to="home">
	  {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
	  </HeaderButton>}
        Дополнительный функционал
      </PanelHeader>
      <Group title="Group title" description="Скоро.. ">
        <List>
          <Cell>
            Календарь
          </Cell>
          <Cell>
            Часы
          </Cell>
        </List>
      </Group>
    </Panel>
  </View>
);

Profile.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Profile;